<script>
// import roadmap from "./components/roadmap.vue";
import tokenomics from "./components/tokenomics.vue";
import partner from "./components/partner.vue";
import locomotiveScroll from "locomotive-scroll";
import Flipbook from "flipbook-vue";
import "locomotive-scroll/dist/locomotive-scroll.css";
export default {
  name: "locoScroll",
  props: {
    msg: String,
  },
  components: { Flipbook, tokenomics, partner },
  data() {
    return {
      pages: [
        null,
        require("./assets/book/page1.png"),
        require("./assets/book/page2.png"),
        require("./assets/book/page3.png"),
        require("./assets/book/page4.png"),
        require("./assets/book/page5.png"),
        require("./assets/book/page6.png"),
        require("./assets/book/page7.png"),
        require("./assets/book/page8.png"),
        require("./assets/book/page9.png"),
        require("./assets/book/page10.png"),
        require("./assets/book/page11.png"),
        require("./assets/book/page12.png"),
        require("./assets/book/page13.png"),
        require("./assets/book/page14.png"),
      ],
      loadingTl: null,
      locoScroll: null,
      logoShake: null,
    };
  },

  mounted() {
    // const _self = this;
    // this.$nextTick(function () {
    //   _self.initLocoScroll();
    // });
    this.initLocoScroll();
    this.initLoading();
    this.logoAni();
    this.roadmap();
  },
  methods: {
    initLocoScroll() {
      const _self = this;
      this.locoScroll = new locomotiveScroll({
        el: _self.$refs["scrollSections"],
        smooth: true,
        smoothMobile: true,
        getDirection: true,
        // for tablet smooth
        // tablet: { smooth: true },
        // for mobile
        // smartphone: { smooth: true },
      });

      // each time Locomotive Scroll updates, tell ScrollTrigger to update too (sync positioning)
      this.locoScroll.on("scroll", this.ScrollTrigger.update);

      // tell ScrollTrigger to use these proxy methods for the ".smooth-scroll" element since Locomotive Scroll is hijacking things
      this.ScrollTrigger.scrollerProxy(".smooth-scroll", {
        scrollTop(value) {
          return arguments.length
            ? _self.locoScroll.scrollTo(value, 0, 0)
            : _self.locoScroll.scroll.instance.scroll.y;
        },
        // we don't have to define a scrollLeft because we're only scrolling vertically.
        getBoundingClientRect() {
          return {
            top: 0,
            left: 0,
            width: window.innerWidth,
            height: window.innerHeight,
          };
        },

        // follwoing line is not required to work pinning on touch screen
        // LocomotiveScroll handles things completely differently on mobile devices - it doesn't even transform the container at all! So to get the correct behavior and avoid jitters, we should pin things with position: fixed on mobile. We sense it by checking to see if there's a transform applied to the container (the LocomotiveScroll-controlled element).
        // pinType: document.querySelector(".smooth-scroll").style.transform
        //   ? "transform"
        //   : "fixed",
      });

      // each time the window updates, we should refresh ScrollTrigger and then update LocomotiveScroll.
      this.ScrollTrigger.addEventListener("refresh", () =>
        _self.locoScroll.update()
      );

      // after everything is set up, refresh() ScrollTrigger and update LocomotiveScroll because padding may have been added for pinning, etc.
      this.ScrollTrigger.refresh();
    },
    initLoading() {
      this.loadingTl = this.gsap.timeline();
      this.loadingTl
        .to(".loading-logo", 1, {
          autoAlpha: 1,
        })
        .to(".loading-logo", 1, {
          autoAlpha: 0,
        })
        .to(".loading-logo", 1, {
          autoAlpha: 1,
        })
        .to(".loading-logo", 1, {
          autoAlpha: 0,
        })
        .to(".loading", 1.5, {
          delay: 1,
          ease: "Power4.easeOut",
          scaleY: 0,
          transformOrigin: "top",
        })
        .from(
          [".intro-content div div"],
          1,
          {
            bottom: "-100%",
            stagger: 0.2,
          },
          "<+=0.5"
        )
        .from(
          ".intro-img",
          1,
          {
            autoAlpha: 0,
            marginTop: "8vw",
          },
          "<+=1"
        )
        .from(
          [".banner"],
          1,
          {
            top: "-20%",
            // stagger: 0.2,
            autoAlpha: 0,
          },
          "<+=0.5"
        )

        .from(
          [".shape1,.shape3"],
          1,
          {
            autoAlpha: 0,
            y: "10%",
            stagger: 0.1,
          },
          "<"
        );
    },
    // closeLoading() {
    //   this.gsap.to(".loading", 1, {
    //     autoAlpha: 0,
    //   });
    // },
    logoAni() {
      var logoShake = this.gsap.timeline({
        repeat: -1,
        paused: true,
        yoyo: true,
      });
      logoShake
        .to(".logo", 0.01, {
          x: 10,
          y: -6,
          color: "#FE2C55",
          scale: 1.5,
          textShadow: "2px -2px #25f4ee",
        })
        .to(".logo", 0.01, {
          x: -10,
          y: 6,
          color: "#25F4EE",
          scale: 1.5,
          textShadow: "2px -2px #FE2C55",
        });

      var logo = document.getElementsByClassName("logo")[0];
      logo.addEventListener("mouseenter", function () {
        logoShake.play(0);
      });
      logo.addEventListener("mouseleave", function () {
        logoShake.pause(0);
      });
    },
    roadmap() {
      /* Main navigation */
      const panelsContainer = document.querySelector("#panels-container");

      /* Panels */
      const panels = this.gsap.utils.toArray("#panels-container .panel");
      this.gsap.to(panels, {
        xPercent: -100 * (panels.length - 1),
        ease: "none",
        scrollTrigger: {
          scroller: this.$refs["scrollSections"],
          trigger: "#panels-container",
          pin: true,
          start: "top top",
          scrub: 1,
          snap: {
            snapTo: 1 / (panels.length - 1),
            inertia: false,
            duration: { min: 0.1, max: 0.1 },
          },
          end: () => "+=" + (panelsContainer.offsetWidth - innerWidth),
        },
      });
    },
    scrollTo(sectionId) {
      this.locoScroll.scrollTo(sectionId);
    },
    scrollToOffset(sectionId) {
      this.locoScroll.scrollTo(sectionId, { offset: 100 });
    },
  },
};
</script>

